const className = 'takeover-transition';

let prevRatio = 0.0;
let increasingColor = 'ratio';
let decreasingColor = 'ratio';

function takeOver(box) {
    let observer;
    let options = {
        root: null,
        rootMargin: '0px',
        threshold: buildThresholdList(),
    };

    observer = new IntersectionObserver(handleIntersect, options);
    observer.observe(box);
}

function buildThresholdList() {
    let thresholds = [];
    let numSteps = 20;

    for (let i = 1.0; i <= numSteps; i++) {
        let ratio = i / numSteps;
        thresholds.push(ratio);
        console.log('ratio: ' + ratio);
    }

    thresholds.push(0);
    return thresholds;
}

function handleIntersect(entries) {
    entries.forEach((entry) => {
        console.log('intersect: ' + entry.intersectionRatio);
        if (entry.intersectionRatio > prevRatio) {
            entry.target.style.opacity = increasingColor.replace(
                'ratio',
                entry.intersectionRatio
            );
        } else {
            entry.target.style.opacity = decreasingColor.replace(
                'ratio',
                entry.intersectionRatio
            );
        }

        prevRatio = entry.intersectionRatio;
    });
}

export default {
    launchFn: takeOver,
    launchQuery: `.${className}`,
};
